.video {
  position: relative;
  padding-bottom: 34.637363%;
  background-color: #000;
  background-size: cover;
  width: 364px;
  height: 0;
  top: -115px;
  left: -65px;
}

.video__media {
  width: 364px;
  height: 241px;
}

.video__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100px;
  height: 100px;
  border: none;
  transform: translate(-50%, -50%);
  background: none;
  padding: 0;
cursor: pointer;
}

.video--enabled {
  cursor: pointer;
}

.video--enable .video__button {
  display: block;
}
