.ask__list {
  list-style-type: none;
  margin: 0;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 0;
}

.ask__item {
  width: 260px;
  border: 1px solid rgba(16, 38, 84, 0.2);
  padding: 18px 88px;
  text-align: center;
  text-align: center;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 100%;
}

.ask__item--checked {
  background: var(--1, linear-gradient(151deg, #ED0233 0%, #003EB7 100%));
  border: none;
}

.ask__link {
  width: 85px;
  margin: 0 auto;
  color: #102654;
}

.ask__link--checked {
  color: #ffffff;
}

.ask__card {
  color: #102654;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 28px;
}

.comment__swiper {
  position: relative;
  width: 580px;
}

.comment__scroll {
  position: relative;
  width: 580px;
  margin: 0 auto;
  margin-top: 37px;
  margin-bottom: 121px;
}

.comment__title {
  width: 155px;
  color: #102654;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0 auto;
  margin-top: 120px;
}

.comment__slide {
  display: grid;
  grid-template-columns: 90px 355px;
  column-gap: 28px;
  background-color: #1C3374;
  padding: 62px;
  width: 580px;
}

.comment__figure {
  background: none;
}

.comment__text {
  width: 355px;
  color: #FFF;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 140%;
}

.comment__pic {
  margin-top: 25px;
}

.comment__name {
  width: 135px;
  color: #FFF;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 20px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px;
}

.comment__description {
  width: 355px;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  margin-top: 25px;
}

.additionall__container {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.free {
  background-color: #102654;
  color: #ffffff;
  padding: 56px 170px 70px 103px;
}

.free__form {
  display: flex;
  flex-direction: column;
  margin-top: 24px;
}

.free__data {
  border: none;
  padding: 0;
  display: flex;
  gap: 30px;
}

.free__title {
  width: 380px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 800;
  line-height: 84px;
  letter-spacing: 1.62px;
  margin: 0;
}

.free__input {
  border: none;
  background: none;
  color: #ffffff;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  border-bottom: 1px solid #74819C;
  width: 190px;
}

.free__input::placeholder {
  width: 60px;
  color: #ffffff;
  opacity: 1;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
}

.free__button {
  margin-top: 30px;
  width: 415px;
  padding: 21px 0;
  color: #ffffff;
  background: none;
  border: 3px solid #ffffff;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: normal;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}

.information {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  padding: 0;
  list-style-type: none;
  column-gap: 120px;
  row-gap: 30px;
  margin: 0;
  margin-top: 35px;
}

.information__item {
  display: flex;
  flex-direction: column;
}

.information__title {
  margin: 0;
  width: 135px;
  color: #102654;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 24px;
}

.information__description {
  width: 195px;
  color: #102654;
  font-family: "roboto", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 152.344%;
  /* 21.328px */
}

.contacts {
  border-top: 1px solid #74819C;
  padding: 56px 100px 70px 100px;
}

.contacts__title {
  width: 180px;
  color: #102654;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-size: 54px;
  font-style: normal;
  font-weight: 600;
  line-height: normal;
  letter-spacing: 1.62px;
  margin: 0;
}

